.home-admin {
    background:#f1f7fc;
    padding: 5vh 0;
    height: 100vh;
    width: 85%;
    margin: 0 auto;
}

.home-admin form {
    max-width:320px;
    width: 90%;
    margin:0 auto;
    background-color:#ffffff;
    padding:40px;
    border-radius:4px;
    color:#505e6c;
    box-shadow:1px 1px 5px rgba(0,0,0,0.1);
}

.home-admin .form-control {
    background:#f7f9fc;
    border:none;
    border-bottom:1px solid #dfe7f1;
    border-radius:0;
    box-shadow:none;
    outline:none;
    color:inherit;
    text-indent:8px;
    height:42px;
}

.home-admin .btn-primary {
    display: block;
    width: 100%;
    margin: 0 auto;
    background:#f4476b;
    border:none;
    border-radius:4px;
    padding:11px;
    box-shadow:none;
    margin-top:26px;
    text-shadow:none;
    outline:none !important;
}

.home-admin form .btn-primary:hover, .home-admin form .btn-primary:active {
    background:#eb3b60;
}